.feedbackForm {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 6px;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  padding: 12px;
  display: flex;
}

.feedbackForm label {
  color: #4a6163;
  flex-direction: column;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  display: flex;
}

.feedbackForm button {
  color: #fff;
  cursor: pointer;
  background-color: #f9a66c;
  border: 1px solid #f9a66c;
  border-radius: 6px;
  padding: 8px 12px;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 600;
}

.feedbackForm button:hover {
  background-color: #f17a7e;
}

input, textarea {
  font: inherit;
  color: #4a6163;
  border: 1px solid #4a6163;
  border-radius: 6px;
  outline: none;
  padding: 8px;
}

input:focus, textarea:focus {
  border: 2px solid #f9a66c;
}

textarea {
  resize: none;
}

